<template>
  <main>
    <nav>
      <a href="#">Simple Todo App</a>
    </nav>
    <div class="container">
      <div class="wrap jcc">
        <div id="todo-list-example">
          <form class="demo" v-on:submit.prevent="addNewTodo">
            <label for="new-todo">Add a todo</label>
            <input
              v-model="newTodoText"
              id="new-todo"
              placeholder="E.g. Feed the cat"
              required
              class="input-add"    
            />
            <input
              v-model="newTodoAt"
              id="new-todo"
              placeholder="E.g. 12 p.m."
              required
              class="input-add"
            />
            <button class="btn-add">Add</button>
          </form>
        </div>
      </div>
    </div>
    <div class="wrap aic jcc">
      <div class="col-todo" v-for="(todo, index) in todos" :key="index">
        <p class="id">{{todo.id}}</p>
        <p class="title">{{todo.title}}</p>
        <p class="at">{{todo.at}}</p>
        <button class="btn-done" v-on:click="remove(index, 1)">Done</button>
      </div>
    </div>
  </main>
</template>
<style scoped>
nav {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem 2rem
}
nav a {
  text-decoration: none;
  font-weight: 800;
  font-size: 1.5rem;
  color: #000
}
