  position: relative;
  display: flex;
  flex-direction: column;
  flex: 0 0 80%;
  max-width: 80%;
  background: #eeeeee;
  margin: 20px 20px;
  padding: 20px 20px;
  border-radius: 10px;
}
.input-add {
  border: 1px solid #cecccc;
  padding: .5rem .8rem;
}
.id {
  position: absolute;
  top: -8px;
  left: -8px;
  background: black;
  color: #fff;
  border-radius: 50px;
  padding: 5px 10px;
}
.title {
  margin-bottom: .5rem;
}
.at {
  margin-bottom: .8rem;
}
.btn-done, .btn-add {
  background: black;
