</style>
<script>
export default {
  data() {
    return {
      newTodoText: '',
      newTodoAt: '',
      time: 'date ' + Date.toLocaleTimeString,
      todos: [
        {
          id: 1,
          title: 'Do the dishes',
          at: '13 a.m.'
        },
        {
          id: 2,
          title: 'Take out the dog',
          at: '13 a.m.'
        },
        {
          id: 3,
          title: 'Feed the cat',
          at: '13 a.m.'
        },
      ],
      nextTodoId: 4
    }
  },
  methods: {
    addNewTodo() {
      this.todos.push({
        id: this.nextTodoId++,
        title: this.newTodoText,
        at: this.newTodoAt
      })
      this.newTodoText = ''
    },
    remove(index) {
      this.todos.splice(index, 1)
    },
  },
}
</script>

